import React from 'react'
import './device-list-item.styles.scss'

const DeviceListItem = ({device, index}) => {
    let mainDisplayText = `Device ${index}`;
    if(device.displayName){
        mainDisplayText = device.displayName;
    } else if(device.uid){
        mainDisplayText = device.uid;
    } if(device.Uuid){
        mainDisplayText = device.Uuid;
    } if(device.id){
        mainDisplayText = device.id;
    }
    return (
        <div className='device-list-item' key={device.id}>
            <h3 className='display-name'>
                {mainDisplayText}
            </h3>
        </div>
    )
}

export default DeviceListItem;
